import { compose, lifecycle, withState, flattenProp } from 'recompose';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import React from 'react';
import styled from 'styled-components';

import { displayLoadingSpinner, hideLoadingSpinner } from '../store/actions';
import { getOrderById } from '../api';
import { PageContainer, ScrollableContainer, Image, roundedBoxShadow, boxSpace,mousePointer } from '../styles';
import MainTopNavbar from '../components/mainTopNavbar';
import ProductListItem from '../components/productListItem'

const CustomerImage = Image.extend`
	border-radius: 8px;
	margin: 8px;
	align-self: center;
`;

const SectionBox = styled.div`
	display: flex;
	flex-direction: column;
	text-align: center;
	${boxSpace};
	${roundedBoxShadow};
	&:not(:first-of-type) {
		margin-top: 0;
	}
`;

const ProductBox = SectionBox.extend`
	${mousePointer};
`

const enhance = compose(
	connect(null, (dispatch) => ({
		displayLoadingSpinner: () => dispatch(displayLoadingSpinner()),
		hideLoadingSpinner: () => dispatch(hideLoadingSpinner())
	})),
	withState('order', 'setOrder', null),
	lifecycle({
		async componentDidMount() {
			this.props.displayLoadingSpinner();
			try {
				const order = await getOrderById(this.props.match.params.id);
				this.props.setOrder(order);
				console.log(order);
			} catch (error) {
				console.log(error);
			}
			this.props.hideLoadingSpinner();
		}
	}),
	flattenProp('order'),
	withRouter
);

const OrderDetailPage = ({ order, product, customer_profile,history }) => {
	return (
		<PageContainer>
			<MainTopNavbar>
				<span
					style={{
						flexGrow: 1,
						textAlign: 'center',
						marginRight: 32
					}}
				>
					รายละเอียดการสั่งซื้อ
				</span>
			</MainTopNavbar>
			<ScrollableContainer>
				{order && (
					<div style={{ display: 'flex', flexDirection: 'column' }}>
						<SectionBox>
							<h2>Customer</h2>
							<CustomerImage src={customer_profile.profile_pic} />
							<h4>
								{customer_profile.first_name} {customer_profile.last_name}
							</h4>
						</SectionBox>
						{/* <ProductBox onClick={()=>history.push(`/product/${product._id}`)}>
							<h2>Product</h2>
							<CustomerImage src={product.images[0]} />
							<h4>{product.name}</h4>
						</ProductBox> */}
						<ProductListItem {...product}/>
					</div>
				)}
			</ScrollableContainer>
		</PageContainer>
	);
};

export default enhance(OrderDetailPage);



