import { compose, lifecycle } from 'recompose';
import { connect } from 'react-redux';
import React from 'react';
import MainBottomNavbar from '../components/mainBottomNavbar';
import MainTopNavbar from '../components/mainTopNavbar';
import { PageContainer, TopNavbarContainer, ScrollableContainer } from '../styles';
import { getOrders } from '../store/actions';
import OrderItem from '../components/orderItem';
const enhance = compose(
	connect(
		({ order: { orders } }) => ({
			orders
		}),
		(dispatch) => ({
			fetchOrders: () => dispatch(getOrders())
		})
	),
	lifecycle({
		componentDidMount() {
			this.props.fetchOrders();
			console.log(this.props.orders);
		}
	})
);

const OrderPage = ({ orders }) => {
	return (
		<PageContainer>
			<MainTopNavbar>
				<span style={{flex:1, textAlign: 'center', marginRight: 32 }}>รายการสั่งซื้อ</span>
			</MainTopNavbar>
			<ScrollableContainer>
				<div>{orders.map((order) => <OrderItem key={order._id} order={order} />)}</div>
			</ScrollableContainer>
			<MainBottomNavbar />
		</PageContainer>
	);
};

export default enhance(OrderPage);



