import React from 'react';
import { TopNavbarContainer } from '../styles';
import BackButton from './backButton';

const MainTopNavbar = ({ children, ...props }) => (
	<TopNavbarContainer {...props}>
		<BackButton />
		{children}
	</TopNavbarContainer>
);

export default MainTopNavbar;



