import React from 'react';
import { BorderedImage, ListItemBox, FlexBoxDown,mousePointer,fadeOnHover } from '../styles';
import styled from 'styled-components';
import { withRouter } from 'react-router-dom';
const DetailBox = FlexBoxDown.extend`padding: 8px;`;
const CustomerProfileImage = styled.img`
	margin: 8px 8px 8px 0;
	border-radius: 8px;
	object-fit: cover;
	width: 32px;
	height: 32px;
`;
const CustomerNameBox = styled.div`
	display: flex;
	align-items: center;
`;

const OrderBox = ListItemBox.extend`
	${mousePointer};
	${fadeOnHover};
`

const OrderItem = ({
	history,
	order: { _id, product: { images, name }, customer_profile: { first_name, last_name, profile_pic } }
}) => {
	return (
		<OrderBox onClick={() => history.push(`/order/${_id}`)}>
			<BorderedImage src={images[0]} />
			<DetailBox>
				<CustomerNameBox>
					<CustomerProfileImage src={profile_pic} alt="" />
					<span>
						{first_name} {last_name}
					</span>
				</CustomerNameBox>
				<h4>{name}</h4>
			</DetailBox>
		</OrderBox>
	);
};

export default withRouter(OrderItem);



