import { call, put, takeLatest } from "redux-saga/effects";
import {
  getOrders
} from "src/api";
import * as types from "../types";

function* fetchOrdersWorker() {
  yield put({ type: types.DISPLAY_LOADING_SPINNER });
  try {
    const Orders = yield call(getOrders);
    console.log(Orders);
    yield put({ type: types.FETCH_ORDERS_SUCCESS, payload: Orders });
    yield put({ type: types.HIDE_LOADING_SPINNER });
  } catch (e) {
    yield put({ type: types.FETCH_ORDERS_ERROR, payload: e });
    yield put({ type: types.HIDE_LOADING_SPINNER });
  }
}





export default [
  takeLatest(types.FETCH_ORDERS, fetchOrdersWorker),
];



