import React, { Component } from 'react';
import { connect } from 'react-redux';
import { PageContainer, ScrollableContainer, TopNavbarContainer } from '../styles';
import MainBottomNavbar from '../components/mainBottomNavbar';
class HomePage extends Component {
	render() {
		return (
			<PageContainer>
				<TopNavbarContainer><span style={{flex:1,textAlign:'center'}}>หน้าหลัก</span></TopNavbarContainer>
				<ScrollableContainer>
					<h2 style={{ textAlign: 'center', marginTop: 36 }}>Welcome To Another Hand</h2>
					<p style={{ textAlign: 'center', marginTop: 12 }}>Let's Add Your Products</p>
				</ScrollableContainer>
				<MainBottomNavbar />
			</PageContainer>
		);
	}
}

export default connect()(HomePage);



