import React from 'react';
import styled from 'styled-components';
import { fadeOnHover, mousePointer, BorderedImage, ListItemBox } from '../styles/';
import { withRouter } from 'react-router-dom';

const ProductListItemBox = ListItemBox.extend`
	${mousePointer};
	${fadeOnHover};
`;

const ProductDetailSide = styled.div`
	display: flex;
	flex-grow: 1;
	flex-direction: column;
	padding: 8px 4px;
	> * {
		margin-bottom: 8px;
	}
`;

const PriceBox = styled.div`
	align-self: flex-end;
	padding-right: 8px;
`;

const Stock = styled.p`margin-top: auto;`;

const ProductListItem = ({ _id, images, name, price, size_type, sizes, description, stock, onClick, history }) => (
	<ProductListItemBox
		onClick={() => {
			history.push(`/product/${_id}`);
		}}
	>
		<BorderedImage src={images[0]} alt="name" />
		<ProductDetailSide>
			<h5 style={{ borderBottom: '1px solid rgba(0,0,0,0.5)' }}>{name}</h5>
			{sizes.length > 0 ? (
				<React.Fragment>
					<ul>
						ไซส์ :
						{sizes.map(({ size, stock }) => (
							<li
								key={size}
								style={{
									display: 'inline-block',
									marginRight: 8,
									textDecoration: stock > 0 ? 'none' : 'line-through'
								}}
							>
								{size}
								{size_type}
							</li>
						))}
					</ul>
					<Stock>เหลือทั้งหมด {sizes.reduce((stock, size) => stock + size.stock, 0)} ชิ้น</Stock>
				</React.Fragment>
			) : (
				<Stock>เหลือทั้งหมด {stock} ชิ้น</Stock>
			)}

			<PriceBox>
				<h4>{sizes.length > 0 ? sizes[0].price : price} ฿</h4>
			</PriceBox>
		</ProductDetailSide>
	</ProductListItemBox>
);

export default withRouter(ProductListItem);



