import * as types from "../types";

const initalState = {
  orders: [],
  error: false,
  errorMessage: ""
};


export const orderReducer = (state = initalState, { type, payload }) => {
  switch (type) {
    case types.FETCH_ORDERS_SUCCESS:
      const orders = payload;
      console.log(orders)
      return {
        ...state,
        orders: orders
      };
      case types.FETCH_ORDERS_ERROR:
      return { ...state, error: true, errorMessage: payload };
    default:
      return state;
  }
};

export default orderReducer;



